<template>
  <el-menu default-active="" 
    class="el-menu-vertical-demo" 
    @open="handleOpen"
    @close="handleClose" 
    :collapse="isCollapse"
    background-color="#545c64"
    text-color="#fff"
    active-text-color="#ffd04b"
  >
 
  <!-- 为一个导航，没有菜单 -->
  <h3 v-show="!isCollapse">通用后台管理系统</h3>
  <h3 v-show="isCollapse">后台</h3>
  <el-menu-item :index="item.path" v-for="item in noChildren" @click='but(item)'>
    <i :class=" 'el-icon-' + item.icon" ></i>
    <span slot="title">{{item.label}}</span>
  </el-menu-item>
  <!-- 为一个下拉菜单 -->
  <el-submenu :index="item.label" v-for="item in hasChilder">
    <template slot="title">
      <i :class=" 'el-icon-' + item.icon"></i>
      <span slot="title">{{item.label}}</span>
    </template>
    <el-menu-item-group>
      <el-menu-item :index="subitem.path" v-for="subitem,subindex in item.children">
        <i :class=" 'el-icon-' + subitem.icon"></i>
        <span slot="title">{{subitem.label}}</span>
      </el-menu-item>
    </el-menu-item-group>
  </el-submenu>
</el-menu>

</template>

<style >
  .el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 200px;
  }
  .el-menu {
    height: 100%;
  }
  h3{
    text-align: center;
    color: #fff;
    padding: 22px 0;
  }
</style>

<script>
  export default {
    data() {
      return {
        menu: [
        {
          path: "/",
          name: "home",
          label: "首页",
          icon: "s-home",
          url: "Home/Home",
        },
        {
          path: "/mall",
          name: "mall",
          label: "商品管理",
          icon: "video-play",
          url: "MallManage/MallManage",
        },
        {
          path: "/user",
          name: "user",
          label: "用户管理",
          icon: "user",
          url: "UserManage/UserManage",
        },
        {
          label: "其他",
          icon: "location",
          children: [
            {
              path: "/page1",
              name: "page1",
              label: "页面1",
              icon: "setting",
              url: "Other/PageOne",
            },
            {
              path: "/page2",
              name: "page2",
              label: "页面2",
              icon: "setting",
              url: "Other/PageTwo",
            },
          ],
        },
      ],
      };
    },
    methods: {
      handleOpen(key, keyPath) {
        console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        console.log(key, keyPath);
      },
      but(item){
        this.$router.push(item.path).catch(err => {
        console.log(err)
        })
      }
    },
    computed:{
      noChildren(){
        return this.menu.filter( item => !item.children)
      },
      hasChilder(){
        return this.menu.filter( item => item.children)
      },
      isCollapse(){
        return this.$store.state.tab.isCollapse
      }
    },
    mounted(){
      
    }
  }
</script>